import api from '../../../common/api.js';

var statusBarHeight = uni.getSystemInfoSync().statusBarHeight + 44 
var animationBoxWidth = uni.getSystemInfoSync().screenWidth - uni.upx2px(60)
export default {
	data() {
		return {
			statusBarHeight: statusBarHeight,
            windowHeight:0,
            animationBoxWidth:animationBoxWidth,
            uploadParams:null,
            facePicTop:0,
            url:null,
            recognizeText:'分析中...',
            recognizeState:0,
            facepic:'',
            nextTitle:'下一步',
            type:0,
            ligulate:"",
            tongueColor:"",
            mossName:"",
            fissureStatus:"",
            mossColor:"",
            bodyfluidName:"",
            indentationStatus:"",
            prick:"",
		}
	},
	onLoad(op) {
        this.windowHeight = uni.getSystemInfoSync().windowHeight
        this.animationBoxWidth = animationBoxWidth
        const data = {
        	purpose: 'useravatar'
        }
        api.getUploadParams({
        	data: data
        }).then(res => {
        	console.log('getUploadParams')
        	console.log(res)
        	this.uploadParams = res.data
        })
        console.log(op.url)
        this.url = op.url
        this.type = op.type
        if(this.type==2){
            this.nextTitle = '完成'
        }
        var _self = this
        
        
	},
	methods: {
        clickNext:function(){
            if (this.uploadParams) {
                this.recognizeState = 1
            	uni.showLoading({
            		title: '图片上传中'
            	})
                var _self = this
            	uni.uploadFile({
            		url: this.uploadParams.url,
            		filePath: this.url,
            		name: 'userAvatar',
            		formData: {
            			policy: this.uploadParams.policy,
            			Signature: this.uploadParams.Signature
            		},
                    success(result) {
            			if (result.data) {
                            console.log(result.data);
            				let r = JSON.parse(result.data)
            				if (r.status === 'OK') {
                                var pic = r.data.location + r.data.filepath
                                _self.recognize(pic)
                                
                    		} else {
                                uni.showToast({
                                    title: r.message,
                                    icon: "none",
                                    duration: 2000
                                })
                            }
                        }
                    },fail() {
                    		                
                    },complete(res) {
                    	uni.hideLoading()
                    	if (res.statusCode !== 200) {
                    		uni.showToast({
                    			title: res.errMsg,
                    			icon: "none",
                    			duration: 2000
                    		})
                    	}
                    }
                })
                 // if(this.type == 0){
                 //     this.recognize('http://coffer.bainuo.cn//useravatar-pub/cf/cfdeaf874723f4ee066ba97f758c10c8.jpg')
                 // }
                 // if(this.type == 1){
                 //     this.recognize('http://coffer.bainuo.cn//useravatar-pub/35/35d0efbfe64c1df9db19a5f5715bcfd0.png')
                 // }
                 // if(this.type == 2){
                 //     this.recognize('https://coffer.bainuo.cn/doctorqualification-pub/23/2309e70f059510ec078ec51d36711a6f.jpg')
                 // }
            }else{
                uni.showToast({
                    title: 'getUploadParams failed!',
                    icon: "none",
                    duration: 2000
                })
            }
        },
        clickDiscriminate:function(){
            var alInfo = uni.getStorageSync('AlInfo');
            if(this.type == 0){
                var tongueImages = {
                    "body":this.facepic
                }
                var info = {
                    "age": alInfo.age,
                    "gender": alInfo.gender,
                    "name": alInfo.name,
                    "phone": alInfo.phone,
                    "weight":alInfo.weight,
                    "height":alInfo.height,
                    "tongueImages":tongueImages
                }
                console.log(info)
                uni.setStorageSync('AlInfo', info)
                uni.navigateTo({
                    url:'tongueFront'
                })
            }else if(this.type == 1){
                var tongueImages = {
                    "body":alInfo.tongueImages.body,
                    "front":this.facepic
                }
                var info = {
                    "age": alInfo.age,
                    "gender": alInfo.gender,
                    "name": alInfo.name,
                    "phone": alInfo.phone,
                    "weight":alInfo.weight,
                    "height":alInfo.height,
                    "tongueImages":tongueImages
                }
                console.log(info)
                uni.setStorageSync('AlInfo', info)
                uni.navigateTo({
                    url:'tongueContrary'
                })
            }else if(this.type == 2){
                var tongueImages = {
                    "body":alInfo.tongueImages.body,
                    "front":alInfo.tongueImages.front,
                    "back":this.facepic,
                    "flag": false,
                }
                var info = {
                    "age": alInfo.age,
                    "gender": alInfo.gender,
                    "name": alInfo.name,
                    "phone": alInfo.phone,
                    "weight":alInfo.weight,
                    "height":alInfo.height,
                    "tongueImages":tongueImages
                }
                console.log(info)
                uni.setStorageSync('AlInfo', info)
                uni.navigateBack({
                    delta:6
                })
            }
            
        },
        clickBack:function(){
            uni.navigateBack({
                delta:1
            })
        },
        recognizeResult:function(key){
            setTimeout(function() {
                
                const getData = {
                	key: key
                }
                if(this.type == 0){
                    api.getFaceImageResult({
                    	data: getData
                    }).then(faceRes => {
                        console.log(faceRes)
                        if(faceRes.status == 'OK'){
                            this.recognizeState = 2
                            this.recognizeText = '分析完成！'
                            // this.ligulate = "快快快"
                        }else if(faceRes.status == '30000'){
                            this.recognizeResult(key)
                        }else{
                            console.log("recognizeFailure")
                            uni.navigateTo({
                                url:'recognizeFailure?msg=' + faceRes.message
                            })
                        }
                    })
                }else{
                    api.getTongueBackImageResult({
                    	data: getData
                    }).then(backres => {
                        console.log(backres)
                        if(backres.status == 'OK'){
                            this.recognizeState = 2
                            this.recognizeText = '分析完成！'
                            // this.ligulate = "快快快"
                        }else if(backres.status == '30000'){
                            this.recognizeResult(key)
                        }else{
                            console.log("recognizeFailure")
                            uni.navigateTo({
                                url:'recognizeFailure?msg=' + backres.message
                            })
                        }
                        
                    })
                }
                
                
                
            }.bind(this), 5000)
        },
        recognize:function(url){
            
            const data = {
            	url: url
            }
            if(this.type == 0){
                api.analysisFaceImage({
                	data: data
                }).then(res => {
                	console.log('analysisFaceImage')
                	console.log(res)
                    if(res.status === 'OK'){
                        
                        this.facepic = url
                        
                        this.recognizeResult(res.data.key)
                        
                        
                    }else{
                        console.log("recognizeFailure")
                        uni.navigateTo({
                            url:'recognizeFailure'
                        })
                    }
                    
                })
            }else if(this.type == 1){
                api.cutTongueImage({
                    data:data
                }).then(res =>{
                    console.log('analysisFaceImage')
                    console.log(res)
                    if(res.status === 'OK'){
                        this.recognizeState = 2
                        this.recognizeText = '分析完成！'
                        this.facepic = url
                        
                        this.ligulate = res.data.ligulate
                        this.tongueColor = res.data.tongueColor
                        
                        console.log(res.data.mossName)
                        var nnn = JSON.parse(res.data.mossName)
                        console.log(nnn)
                        if(nnn && nnn.length>0){
                            nnn.forEach((item,index)=>{
                                if(index == 0){
                                    this.mossName = this.mossName + item
                                }else{
                                    this.mossName = this.mossName + ',' +item
                                }
                            })
                        }
                        // this.mossName = res.data.mossName
                        if(!res.data.fissureStatus){
                            this.fissureStatus = "无裂纹"
                        }else{
                            this.fissureStatus = "有裂纹"
                        }
                        this.fissure = res.data.fissure
                        this.mossColor = res.data.mossColor
                        this.bodyfluidName = res.data.bodyfluidName
                        if(!res.data.indentationStatus){
                            this.indentationStatus = "无齿痕"
                        }else{
                            this.indentationStatus = "有齿痕"
                        }
                        
                        if(!res.data.prickStatus){
                            this.prick = "无刺点"
                        }else{
                            this.prick = "有刺点"
                        }
                        
                        // const getData = {
                        // 	key: res.data
                        // }
                        // api.getTongueResult({
                        // 	data: getData
                        // }).then(res => {
                            
                        // })
                        
                    }else{
                        console.log("recognizeFailure")
                        uni.navigateTo({
                            url:'recognizeFailure'
                        })
                    }
                })
            }else if(this.type == 2){
                api.cutTongueBackImage({
                    data:data
                }).then(res =>{
                    console.log('analysisFaceImage')
                    console.log(res)
                    if(res.status === 'OK'){
                        
                        this.facepic = url
                        this.recognizeResult(res.data.key)
                    }else{
                        console.log("recognizeFailure")
                        uni.navigateTo({
                            url:'recognizeFailure'
                        })
                    }
                })
            }
            
        }
    }
}
